.transactionHistory {
    margin-left: auto;
    margin-right: auto;
    padding: 20px;
    text-align: center;
    background-color: rgb(229, 249, 198);;
    width: 600px;
    font-family: 'Roboto', sans-serif;
    border-radius: 6px;
    box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.12), 0px 4px 4px rgba(0, 0, 0, 0.06),
    1px 4px 6px rgba(0, 0, 0, 0.16);
    margin-top: 40px;
    margin-bottom: 40px;
}

.transactionHistory td {
    padding: 10px;
    border: 1px solid #212121;
    background-color: #f3f2f2;
}
.transactionHistory td:hover {
    color: rgb(244, 238, 238);
    background-color: #21cafe;
}

.transactionHistory th {
    font-weight: 600;
    padding: 10px;
    border: 1px solid #212121;
    background-color: #21cafe;
}